import Head from 'next/head'
import Layout from '../components/layout'
import React, { Component, Fragment } from 'react'
import api from '../api';
import Slider from "react-slick";

class Index extends Component {
  static async getInitialProps() {
    const index = await api.posts().categories(2).embed();
    return { index };
  }

  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
	  slidesToScroll: 1,
	  autoplay: true,
      autoplaySpeed: 2000
    };
    return (
      <Fragment>
        <Layout>

        <Head>
          <title>This is our page title!</title>
          <meta name="description" content="This is an example of a meta description. This will show up in search results." />
          <meta charSet="utf-8" />
          <meta name="viewport" content="initial-scale=1.0, width=device-width" />
        </Head>
        <div id="hero">
		<Slider {...settings}>  
       	 {
            this.props.index.map( post => {
              return (
                <div key={ post.id }>
                    <div>
                      <img className="img-fluid" src={ post.better_featured_image.source_url } alt=""/>
                    </div>
                </div>
              )
            })
          } 
		  </Slider>
        </div> 
        <div className="container-fluid fix">
          <h1>Your new server-side rendered React.js app!</h1>
         
          
        </div>
        </Layout>
      </Fragment>
   )
  }
}
 
export default Index;